import InsitesMixins from './InsitesMixins.js';
import DatabaseManagerMixins from './DatabaseManagerMixins.js';

export default {
    mixins: [InsitesMixins, DatabaseManagerMixins],
    methods: {
        convertToCustomizationName(file_path) {            
            return "modules/" + this.getModuleName(file_path) + "/" + this.getName(file_path);
        },
        buildFormData(data, id) {			
            let formData = { "payload": {} };          
            if(id) { // Delete or Update customization ID 
                formData.payload['id'] = id;                
            }
            if(data) { // Create customization
                formData.payload['customization'] = {};
                formData.payload['customization']['custom_model_type_name'] = data.name,
                formData.payload['customization']['properties'] = [];
       
                for (let key in data.properties) {
                    formData.payload['customization']['properties'].push(
                        { 'name': key, 'value': typeof data.properties[key] === "string" ? data.properties[key] : JSON.stringify(data.properties[key]) }
                    );
                }
            } else { 
                this.payloadErrorHandler("Missing data fields.");
            }

            return JSON.stringify(formData);            
        },
        payloadErrorHandler(message) {
            toastIt("error", `Error invalid form data. ${message}`, InsitesUtil.ops());
        },
        dynamicSort (property, key1, order) {
            let sortOrder = "desc";
            if(order) sortOrder = order;
            
            return function (a,b) {
                b = b[key1][property];
                a = a[key1][property];
                if(sortOrder === "desc") {
                    return b > a ? 1 : -1;  
                } else {
                    return a < b ? -1: 1;
                }        
            }
        }
    }
}