import InsitesMixins from './InsitesMixins';
import { uniqBy } from "lodash";

export default {
    mixins: [InsitesMixins],
    data() {
        return {
            dir: "modules/ins_database/public"
        }
    },
    methods: {
        convertToDatabaseFileName(name) {
            return InsitesUtil.removeWhitespace(name).trim().replace(/[ -]/g, "_").replace(/[^a-zA-Z0-9_]/g, "").toLowerCase();
        },
        convertToModuleName(name) {
            return InsitesUtil.removeWhitespace(name).replace(/[^a-zA-Z0-9]/g,"_").toLowerCase();
        },
        setDatabasePhysicalPath(name) {
            let file_name = this.convertToDatabaseFileName(name);
            return file_name !== '' ? `${this.dir}/custom_model_types/${file_name}.yml`: '';
        },
        setFormConfigurationPhysicalPath(name) {
            let file_name = this.convertToDatabaseFileName(name);
            return file_name !== '' ? `${this.dir}/form_configurations/${file_name}.liquid`: '';
        },
        setFormConfigurationName(name) {
            return `modules/ins_database/${this.convertToModuleName(name)}`;
        },
        getFormConfigurationName(path) {
            return path.split('/')[path.split('/').length - 1];
        },
        buildDatabaseSchema(name, custom_model_name, properties, tableHeader, physical_file_path) {
            let data = { 
                "payload": {
                    "schema": {
                        "name": this.getFormConfigurationName(custom_model_name),
                        "metadata" : {
                            "is_enabled": "true",
                            "label": InsitesUtil.removeWhitespace(name)
                        },
                        "properties": this.setProperties(properties, tableHeader)
                    }
                }
            };

            if (!physical_file_path) {
                data.payload.schema.physical_file_path = this.setDatabasePhysicalPath(this.getFormConfigurationName(custom_model_name));
            }

            return data;
        },
        setProperties(properties, tableHeader) {
            let fields = this.buildFieldProperties(properties);
            return this.setTableHeader(fields, tableHeader);
        },
        buildFieldProperties(properties) {
            let fields = [];
            for (let counter = 0; counter < properties.length; counter++) {
                fields[counter] = {};
                fields[counter].default_value = properties[counter].default_value;
                fields[counter].id = properties[counter].id;
                fields[counter].name = properties[counter].id ? properties[counter].name : this.convertToModuleName(properties[counter].name);
                fields[counter].metadata = properties[counter].metadata;
            
                if (!properties[counter].id) {
                    fields[counter].attribute_type = properties[counter].attribute_type;
                }
            }

            return fields;
        },
        setTableHeader(properties, tableHeader) {
            for (let counter = 0; counter < properties.length; counter++) {
                if (counter === tableHeader) {
                    properties[counter].metadata.is_header = "true";
                }
            }

            return properties;
        },
        setFieldMetadata(data, counter, id, weight) {
            return {
                "attribute_type": data.metadata.attribute_type,
                "counter": counter,
                "default_value": null,
                "id": id ? id : null,
                "name": id ? data.name : this.convertToModuleName(data.name),
                "label": data.label,
                "metadata": {
                    "label": InsitesUtil.removeWhitespace(data.label),
                    "group_id": data.group_id,
                    "type": data.metadata.id,
                    "is_header": "false",
                    "is_required": data.metadata.is_required ? "true" : "false",
                    "is_enabled": "true",
                    "status": "enabled",
                    "values": data.values ? uniqBy(data.values.split(',')) : [],
                    "weight": id ? weight : counter
                },
                "is_required": data.mandatory ? "true" : "false"
            };
        },
        buildFormConfigurationSchema(custom_model_name, label, physical_path, properties) {
            return {
                "payload": {
                    "schema": {
                        "name": this.getFormConfigurationName(custom_model_name),
                        "physical_file_path": physical_path,
                        "resource": "Customizations",
                        "resource_owner": "anyone",
                        "authorization_policy_ids": [],
                        "metadata": {
                            "is_enabled": "true",
                            "label": InsitesUtil.removeWhitespace(label)
                        },
                        "configuration": {
                            "properties": this.setFormCongurationProperties(properties)
                        }
                    }
                }
            }
        },
        setFormCongurationProperties(properties) {
            let fields = {};

            for (let counter = 0; counter < properties.length; counter++) {
                if (properties[counter].metadata.is_required === "true") {
                    fields[properties[counter].name] = {};
                    fields[properties[counter].name].validation = {};
                    fields[properties[counter].name].validation.presence = true;
                } else {
                    fields[properties[counter].name] = null;
                }
            }

            return fields;
        },        
        invalidParamIDHandler(data = "") {
            toastIt("error", `Failed to get ${data} info. Invalid ${data} ID or does not exist.`, InsitesUtil.ops());
            this.redirectToDBList();
        },
        noDatabaseErrorHandler() {      
            toastIt("error", "Failed to get Database fields.", InsitesUtil.ops());
            this.redirectToDBList();
        },
        redirectToDBList() {
            InsitesUtil.renderPage(`Database List`, `#/database`);
            this.$router.push(`/database`);
        }
    }
}
