<template>
    <ins-radio ref="insRadioEl"
        v-bind:label="label"
        v-bind:name="name"
        v-bind:checked="is_checked"
        @onSelect="onSelectChange">
    </ins-radio>
</template>

<script>
export default {
    name: "InsRadioWrap",
    props: {
        label: String,
        name: String,
        value: String,
        disabled: Boolean,
        checked: Boolean
    },     
    data() {
        return {
            is_checked: false
        }
    },
    methods: {
        onSelectChange(event) {
            this.is_checked = event.detail.checked;
            this.$emit("input", this.label);
            this.$emit("event", event.detail);
        }
    },
    mounted() {
        this.is_checked = this.checked;
    }
}
</script>

