<template>
  <div class="grid-container-full pages">
    <div id="databaseItem" class="grid-x">
      <div class="large-9 medium-12 small-12 cell" v-if="showPage">
          <form id="dbItemForm" novalidate onsubmit="event.preventDefault()">
          <ins-card steady class="height-auto" v-if="databaseFields.length">
            <div class="grid-x">
              <div class="large-9 medium-9 small-12 cell">
                <h2 class="h2desc">Details</h2>
              </div>
              <div class="large-3 medium-3 small-12 cell padding-right--small">
                <!-- <div class="flex-align-right">
                  <InsToggleWrap class="text-left" label="Enable Item" v-bind:checked="true"/>
                </div> -->
              </div>
            </div>

            <DynamicFields
              v-for="field in databaseFields"
              v-bind:key="field.id"
              v-bind:name="field.name"
              v-bind:label="objectHas(field.metadata, 'label') ? objectHas(field.metadata, 'label') : field.name"
              v-bind:is_required="objectHas(field.metadata, 'is_required')"
              v-bind:weight="objectHas(field.metadata, 'weight')"
              v-bind:values="objectHas(field.metadata, 'values')"
              v-bind:type="objectHas(field.metadata, 'type') ? objectHas(field.metadata, 'type') : field.attribute_type"
              v-bind:data="pageElementData[0]"
              v-on:checkboxInputChange="listCheckBoxHandler"
              v-on:radioInputChange="listRadioHandler"
              v-on:dateInputChange="dateInputHandler"
            />
          </ins-card>
          <div class="spacer"></div>
          <div class="grouped-fields" ref="item-field-groups" v-if="showPage && fieldGroups.length">
            <div class="group-card" v-for="(group, index) in fieldGroups" v-bind:key="group.id">
                <ins-card steady class="height-auto">
                  <h2 class="h2desc">{{ group.field_label }}</h2>

                  <DynamicFields
                    v-for="field in group.fields"
                    v-bind:key="field.id"
                    v-bind:name="field.name"
                    v-bind:label="objectHas(field.metadata, 'label') ? objectHas(field.metadata, 'label') : field.name"
                    v-bind:is_required="objectHas(field.metadata, 'is_required').toString()"
                    v-bind:weight="objectHas(field.metadata, 'weight')"
                    v-bind:values="objectHas(field.metadata, 'values')"
                    v-bind:type="objectHas(field.metadata, 'type') ? objectHas(field.metadata, 'type') : field.attribute_type"
                    v-bind:data="pageElementData[0]"
                    v-on:checkboxInputChange="listCheckBoxHandler"
                    v-on:radioInputChange="listRadioHandler"
                    v-on:dateInputChange="dateInputHandler"  />
                </ins-card>
              <div class="spacer" v-if="index !== fieldGroups.length-1"></div>    
            </div>           
        </div>
        <div class="grid-x grid-padding-y">
          <div class="large-4 medium-5 small-12 cell footer-btn-container">
            <ins-button
              v-if="formType!=='Add'"
              v-bind:disabled="bindingValues"
              v-on:onClickInsButton="archiveItem"
              color="negative"
              label="Delete"
            ></ins-button>
          </div>
          <div class="large-4 large-offset-4 medium-5 medium-offset-2 small-12 cell text-right footer-btn-container">
            <ins-button outlined color="blue" label="Cancel" 
              v-bind:disabled="bindingValues"
              v-on:onClickInsButton="goBackHandler">
            </ins-button>
            <ins-button
              solid
              color="blue"
              v-bind:label="formType"
              v-bind:disabled="bindingValues"
              v-on:onClickInsButton="submitFormHandler">
            </ins-button>
            <ins-button type="button" v-if="formType == 'Add'"
              label="Save and Add Another" color="blue" solid="true" size="normal"
              v-bind:disabled="bindingValues"
              v-on:onClickInsButton="saveAddMore()">
            </ins-button>
          </div>
        </div>
        </form>
      </div>
      
      <div v-else class="large-9 medium-12 small-12 cell">        
          <ins-card steady>
            <div class="height-full vertical-align-middle">
              <ins-loader
                state-title="Processing"
                state-message="Please wait, we're already processing your data."
                state-icon="processing">
              </ins-loader>  
            </div>    
          </ins-card>
      </div>
    </div>
  </div>
</template>
<script>
import { uniqBy, flattenDeep, remove, find, sortBy } from "lodash";
import InsToggleWrap from "../../components/InsToggleWrap/InsToggleWrap.vue";

import InsitesMixins from "../../mixins/InsitesMixins";
import DatabaseItemsMixins from '../../mixins/DatabaseItemsMixins.js';
import DatabaseManagerMixins from '../../mixins/DatabaseManagerMixins.js'

import {
  fetchDatabaseDetailsJSON,
  fetchDatabaseDetailsbyNameJSON,
  fetchDatabaseFieldGroupsJSON,
  fetchDatabaseItemDetailsJSON,
  fetchFieldGroupDetailsJSON,
  deleteDatabaseItem,
  updateDatabaseItem,
  addDatabaseItem
} from "../../services/DatabaseServices";
import {
  sidebarMenuDatabaseRoute,
  listDatabaseRoute,
  listDatabaseItemsRoute
} from "../../models/InsDatabaseRoutes.js";

import DynamicFields from "../../components/_Database/DynamicFields/DynamicFields.vue";

export default {
  name: "ManageDatabaseItemDetails",
  components: {
    InsToggleWrap,
    DynamicFields
  },
  mixins: [InsitesMixins, DatabaseItemsMixins, DatabaseManagerMixins],
  data() {
    return {
      itemID: "",
      databaseID: "",
      datatabaseName: "",
      storedDatabaseFields: {},
      databaseFields: [],
      pageElementData: [],
      fieldGroups: [],
      ungroupedFields: [],
      showPage: false,
      size: 100,
      current_page: 1,
      total_pages: 1,
      formType: "Add",
      bindingValues: false,
      addMore: false
    };
  },
  methods: {
    goBackHandler() {
      InsitesUtil.goBack();
    },
    saveAddMore() {
      this.addMore = true;
      this.submitFormHandler();
    },
    dateInputHandler(event) {
      
    },
    listCheckBoxHandler(event) {   
      // let elements = document.querySelectorAll(`.ins-checkbox-wrap input[name="${event.name}"]`);
      // let values = [];

      // // convert NodeList to Array
      // // elements = Array.prototype.slice.call(elements);

      // for (let elem in elements) {
      //   if (elements[elem].checked) {
      //     values.push(elements[elem].nextSibling.innerText);
      //   }
      // }

      // this.pageElementData[0].properties[event.name] = values.join(',');
      // this.validateSelectOptions(event.name);

        let temp = this.pageElementData[0].properties[event.name] ? this.pageElementData[0].properties[event.name].trim() !== "" 
          ? this.pageElementData[0].properties[event.name].trim().split(',') 
          : [] : [];

        temp = uniqBy(temp);
        if (event.checked) {
            temp.push(event.value);
        } else {
            let data = temp.splice(temp.indexOf(event.value), 1);
        } 
        this.pageElementData[0].properties[event.name] = temp.join(',');
        this.validateSelectOptions(event.name);
    },
    listRadioHandler(event){
      this.validateSelectOptions(event.name);
    },
    validateSelectOptions(groupName) {
        let group = document.querySelectorAll(`.required[group-name='${groupName}']`);
        if (this.pageElementData[0].properties[groupName] === "") {
            group[0].classList.add('is-invalid');
        } else {
            group[0].classList.remove('is-invalid');
        }
    },
    archiveItem() {
      swal({
        title: "",
        html: `
        <ins-loader
          state-icon="warning"
          icon-color="warning"
          state-message="Are you sure you want to delete this item?">
        </ins-loader>`,
        type: "",
        width: 330,
        showCancelButton: true,
        confirmButtonClass: "negative",
        confirmButtonText: "Delete"
      }).then(result => {
        if (result.value){
          this.bindingValues = true;
          this.deleteItem("Item has been deleted.");
        }
      });
    },
    async deleteItem(message) {
      let response;
      let formData = this.buildFormData(this.pageElementData[0], this.itemID);
      let deleteItem = await deleteDatabaseItem(formData)
        .then(response => {
          this.successHandler(response, message);
        })
        .catch(error => {
          this.errorHandler(error, 'delete');
        });
    },   
    submitFormHandler() {
      this.bindingValues = true;
      let form = document.getElementById('databaseItem');
      let fields = Array.from($(form).find((".ins-datepicker__date, .ins-checkbox-group-wrapper.required, .ins-radio-group-wrapper.required, .ins-form-field, ins-select, .ins-textarea-field")));

			if(InsitesUtil.formValidate(fields)) {
        if(this.formType === 'Add') {
          this.createItemHandler();
        } else {
          this.updateItemHandler();
        }        
      } else {        
        this.addMore = false;
        this.bindingValues = false;
        this.submittingForm = false;
      }  
    },
    async createItemHandler() {
      let formData = this.buildFormData(this.pageElementData[0], this.itemID);
      let createItem = await addDatabaseItem(formData)
        .then(response => {
          this.successHandler(response, 'Item has been updated.');
        })
        .catch(error => {
          this.errorHandler(error, 'create');
        });
    },
    async updateItemHandler() {
        let formData = this.buildFormData(this.pageElementData[0], this.itemID);
        let updateItem = await updateDatabaseItem(formData)
          .then(response => {
            this.successHandler(response, 'Item has been updated.');
          })
          .catch(error => {
            this.errorHandler(error, 'update');
          });
    },
    updatePageElementData(data) {
			for (let key in data.properties) {
				let dataField = find(this.storedDatabaseFields.database_fields, (item) => { return item.name === key; });
				switch(dataField.attribute_type) {
					case "date":
						let converted_date = moment(data.properties[key], 'MM/DD/YYYY').format('YYYY-MM-DD');
						data.properties[key] = converted_date !== "Invalid date" ? converted_date : data.properties[key];
						break;
					case "string":
						data.properties[key] = data.properties[key];
						break;
					default:
						data.properties[key] = JSON.stringify(data.properties[key]);
				}
			}

			return data;
    },
    successHandler(response, message) {
      toastIt('success', `${message}`, InsitesUtil.ops());
      document.getElementById("dbItemForm").reset();
      this.bindingValues = false;    
      this.preSetFormData();
      !this.addMore ? InsitesUtil.goBack() : "";
      this.addMore = false;
    },
    errorHandler(error, message) {
      console.error("Form submit error: ", error);
      toastIt("error", `Failed to ${message} item. Please try again.`, InsitesUtil.ops());
      this.bindingValues = false;
      this.addMore = false;
    },
    preSetFormData() {
      let properties = {}; 
      this.storedDatabaseFields.database_fields.forEach(item => {
        let type = item.metadata ? item.metadata.type : item.attribute_type.toLowerCase();
        properties[item.name] = (type === 'toggle' || type === 'boolean') ? 'true' : '';
      });
      
      this.pageElementData = [{
        "name": this.convertToCustomizationName(this.storedDatabaseFields.physical_file_path),
        properties
      }];
    },
    renderPage() {      
      this.formType === "Add" ? this.preSetFormData() : '';
      this.showPage = true;
    },
    assignToFieldGroup() {
      let fieldData = this.databaseFields;
      this.fieldGroups.forEach(item => {
        item["fields"] = [];
        item["fields"] = remove(fieldData, field => {
          if (field.metadata) {
            if (field.metadata.group_id) {
              if (item.id === field.metadata.group_id) {
                return field;
              }
            }
          }
        });
      });

      this.sortGroupFields();

      console.log("groups", this.fieldGroups);
      console.log("data", this.pageElementData);
      this.renderPage(); // END, Render page
    },
    sortGroupFields() {
      this.setDefaultGroupFieldsWeight();
      for (let key in this.fieldGroups) {
        let sortedGroupFields = this.fieldGroups[key].fields;

        sortedGroupFields = sortBy(sortedGroupFields, (item) => {
          return item.metadata.weight;
        });
        this.fieldGroups[key].fields = sortedGroupFields;
      }
    },
    setDefaultGroupFieldsWeight() {
      for (let key in this.fieldGroups) {
        for (let field_key in this.fieldGroups[key].fields) {
          if (!this.fieldGroups[key].fields[field_key].metadata) {
            this.fieldGroups[key].fields[field_key].metadata = {};
          }

          if (typeof this.fieldGroups[key].fields[field_key].metadata.weight !== "number") {
            this.fieldGroups[key].fields[field_key].metadata.weight = 0;
          }
        }
      }
    },
    getFieldMetadata(field) {
      let metadata = null;
      this.databaseFields.forEach(item => {
        if (field === item.name) {
          metadata = item.metadata;
        }
      });
      return metadata;
    },
    async fetchDatabaseItemFieldValue() {
      // get data values
      let itemData = await fetchDatabaseItemDetailsJSON(this.itemID);
      if (itemData.state) {
        if(itemData.results.items.results.length) { 
          let data = JSON.parse(JSON.stringify(itemData.results.items.results));
          this.pageElementData = data;    
          await this.fetchDatabaseFields(); // Step 2: Fetch Database Fields      
					for (let counter = 0; counter < data.length; counter++) {
						data[counter] = this.updatePageElementData(data[counter]);
					}
          this.pageElementData = data;    
        } else {
          this.invalidParamIDHandler("item");
        }
      } else {
        this.invalidParamIDHandler("item");
      }
    },
    async fetchDatabaseFields() {
      let dbFieldsList = 
        !!this.databaseID ? await fetchDatabaseDetailsJSON(this.databaseID)
                        : await fetchDatabaseDetailsbyNameJSON(this.pageElementData[0].name);
       
      if (dbFieldsList.state) {
        if (dbFieldsList.results.items.results.length) {
          let data = JSON.parse(JSON.stringify(dbFieldsList.results.items.results[0].database_fields));
          this.storedDatabaseFields = JSON.parse(JSON.stringify(dbFieldsList.results.items.results[0]));
          if(data.metadata) {
            if(data.metadata.weight)
              data.sort(this.dynamicSort('weight', 'metadata', 'asc'));
          }
          this.databaseFields = data;
          this.setBreadCrumbs();   
          this.setFieldGroups(); 
        } else {
          this.noDatabaseErrorHandler();
        } 
      } else {
        this.noDatabaseErrorHandler();
      }
    },
    setFieldGroups() {
        let count = 0;
        this.databaseFields.forEach(async item => {
          // Step 2: get field_group details & set UNIQUE group array
          if (item.metadata) {
            if(item.metadata.group_id)
             await this.checkGroupField(item.metadata.group_id);             
          }
          count ++;
          if (count === this.databaseFields.length) {
            this.assignToFieldGroup();
          }
        });
    },
    async checkGroupField(id) {
      let groupData = await fetchFieldGroupDetailsJSON(id);

      if (groupData.results.items.results) {
        if (window.location.hostname === "localhost") {
          this.updateFieldGroups(find(groupData.results.items.results, (item) => { return item.id === id; }));
        } else {
          this.updateFieldGroups(groupData.results.items.results[0]);
        }
      }
    },
    updateFieldGroups(data) {
      // make fieldGroups[] Uniq - by: group id
      data ? this.fieldGroups.push(data) : "";
      this.fieldGroups = flattenDeep(this.fieldGroups);
      let tempArr = uniqBy(this.fieldGroups, "id");
      tempArr = sortBy(tempArr, (item) => {
        return item.field_label;
      });
      this.fieldGroups = tempArr;
    },
    setBreadCrumbs() {
      let crumbs = [];
      crumbs = [sidebarMenuDatabaseRoute, listDatabaseRoute];
      crumbs.push({
        link: `#/list-database-items/${this.storedDatabaseFields.id}`,
        app: false,
        withSubmenu: false,
        label: `${this.storedDatabaseFields.name}`
      });

      this.formType === "Update"
      ? crumbs.push({
          label: "Item Details",
          link: `#${this.$route.path}`,
          withSubmenu: false,
          app: false
        })
      : crumbs.push({
          label: "Add New Item",
          link: `#${this.$route.path}`,
          withSubmenu: false,
          app: false
        });
      InsitesUtil.setBreadCrumbs(crumbs);
    },
    async setFormConfiguration() {
      if (this.$route.path.includes("/add-database-item")) {
        // add new Item
        this.formType = "Add";
        this.checkURLParamsID() 
          ? this.databaseID = this.checkURLParamsID()
          : this.invalidParamIDHandler("database");
        
        this.fetchDatabaseFields(); // Step 2: Fetch Database Fields
      } else {
        // edit Item
        this.formType = "Update";
        if(this.checkURLParamsID()) {
            this.itemID = this.checkURLParamsID();
            await this.fetchDatabaseItemFieldValue(); // STEP 1: Fetch database field config
        } else {
          this.invalidParamIDHandler("item");
        }
      }
    }
  },
  created() {
    this.setFormConfiguration();    
  }
};
</script>
<style lang="scss" src="./ManageDatabaseItemDetails.scss"></style>