import Vue from "vue";
import Router from "vue-router";

// Database Modules
import FieldGroups from "./views/FieldGroups/FieldGroups.vue";
import ListDatabase from "./views/ListDatabase/ListDatabase.vue";
import ManageDatabase from "./views/ManageDatabase/ManageDatabase.vue";
// Database data 
import ListDatabaseItems from "./views/ListDatabaseItems/ListDatabaseItems.vue";
import ManageDatabaseItemDetails from "./views/ManageDatabaseItemDetails/ManageDatabaseItemDetails.vue";
import ImportData from "./views/ImportData/ImportData.vue";

Vue.use(Router);

export default new Router({
  routes: [    
    // Database Modules
    {
      path: "/field-groups",
      name: "FieldGroups",
      component: FieldGroups
    },
    {
      path: "/database",
      name: "ListDatabase",
      component: ListDatabase
    },
    {
      path: "/manage-database",
      name: "ManageDatabase",
      component: ManageDatabase
    },
    {
      path: "/edit-database/:id?",
      name: "EditDatabase",
      component: ManageDatabase
    },
    {
      path: "/list-database-items/:id?",
      name: "ListDatabaseItems",
      component: ListDatabaseItems
    },
    {
      path: "/add-database-item/:id?",
      name: "ManageDatabaseItemDetails",
      component: ManageDatabaseItemDetails
    },
    {
      path: "/edit-database-item/:id?",
      name: "EditDatabaseItemDetails",
      component: ManageDatabaseItemDetails
    },
    {
      path: "/import-data/:id?",
      name: "ImportData",
      component: ImportData
    },
  ]
});
