<template>
    <ins-checkbox ref="insCheckboxEl"
        v-bind:label="label"
        v-bind:name="name"
        v-bind:value="value"
        v-bind:checked="is_checked"
        @onCheckInsCheckbox="onSelectChange"
    ></ins-checkbox>
</template>

<script>
export default {
    name: "InsCheckboxWrap",
    props: {
        label: String,
        name: String,
        value: String,
        disabled: Boolean,
        checked: Boolean
    },
    data() {
        return {
            is_checked: false
        }
    },
    methods: {
        onSelectChange(event) {
            this.is_checked = event.detail.checked;
            this.$emit("input", event.detail);       
        }
    },
    mounted() {
        this.is_checked = this.checked;
    }
}
</script>

