<template>
     <div class="grid-container-full pages">
        <div class="grid-x">
            <div class="large-12 cell items-list-container">
            <!-- database filters -->
            <div class="database-filter">
            </div>
            <!-- end database filters -->
            <!-- no data placeholder -->
                <div v-show="!hasData"> 
                    <div class="pages-bottom">
                        <h2 class="pages-title-secondary h2desc">You don't have any items yet.</h2>
                        <p class="pdesc">Would you like to create your first item?</p>

                        <ins-button label="Add New Item" solid
                            v-on:onClickInsButton="goToAddNewDatabaseItem">
                        </ins-button>
                    </div>
                </div>
                <!-- end no data placeholder -->
                <!-- database items list table -->
                <div class="table-list-container" v-show="hasData">
                    <div class="header-buttons">
                        <ins-button label="Add New Item" solid
                            color="primary" size="normal"
                            v-on:onClickInsButton="goToAddNewDatabaseItem">
                        </ins-button>
                        <!-- <ins-button label="Import Data"
                            color="primary" size="normal" outlined
                            v-on:onClickInsButton="goToImportDatabase">
                        </ins-button> -->
                        <ins-button label="Edit Database Settings"
                            color="primary" size="normal" outlined
                            v-on:onClickInsButton="goToEditDatabase">
                        </ins-button>
                    </div>

                    <div class="ins-table-container">
                        <ins-base-table ref="insBaseTable"
                        v-bind:searchbar-placeholder="`Search by ${tableHeader ? tableHeader : 'ID' }`"
                        v-on:onPaginationChange="tablePaginationHandler"
						v-on:onTableSort="tableSortHandler"
                        v-on:onTableSearch="tableSearchHandler"
                        v-on:onTableBulkAction="tableBulkActionHandler"
                        v-on:onTableRowAction="tableRowActionHandler">
                        </ins-base-table>
                        <div class="spacer large"></div>
                    </div>
                </div>
            <!-- end database items list table -->
            </div>
        </div>
     </div>
</template>
<script>
    import { fetchDatabaseDetailsJSON, fetchDatabaseItemsListJSON, deleteDatabaseItem, searchDatabaseItemIDListJSON, searchDatabaseItemsListJSON } from '../../services/DatabaseServices.js';
    import { sidebarMenuDatabaseRoute, listDatabaseRoute, listDatabaseItemsRoute } from '../../models/InsDatabaseRoutes.js';
    import InsitesMixins from '../../mixins/InsitesMixins.js';
    import DatabaseManagerMixins from '../../mixins/DatabaseManagerMixins.js';
    import DatabaseItemsMixins from '../../mixins/DatabaseItemsMixins.js';

    export default {
        name: "ListDatabaseItems",
        mixins: [DatabaseManagerMixins, DatabaseItemsMixins, InsitesMixins],
        data() {
            return {
                hasData: false,
                databaseID: this.checkURLParamsID(),
                databaseName: "",
                databasePhysicalPath: "",
                itemList: [],
				pageSize: 10,
				pageNumber: 1,
				sortedKeyword: "",
				sortedBy: "asc",
				searchKeyword: "",
				continueDeletion: false,
                is_enabled: "true",
                customization_url: "/api/customizations",
                tableHeader: null,
                tableHeaderName: null
            }
        },
        methods: {
            goToAddNewDatabaseItem(event) {
                InsitesUtil.renderPage(`Add New Item`, `#/add-database-item/${this.checkURLParamsID()}`);
				this.$router.push(`/add-database-item/${this.checkURLParamsID()}`);
            },
            goToManageDatabaseItem(data, action) {
                InsitesUtil.renderPage(`${action} Page`, `#/edit-database-item/${data.id}`);
				this.$router.push(`/edit-database-item/${data.id}`);
            },
            goToImportDatabase(event) { 
                InsitesUtil.renderPage(`Import Data`, `#/import-data/${this.checkURLParamsID()}`);
				this.$router.push(`/import-data/${this.checkURLParamsID()}`);
            },
            goToEditDatabase(data, action) {
                InsitesUtil.renderPage(`Edit ${this.capitalize(this.databaseName)}`, `#/edit-database/${this.checkURLParamsID()}`);
				this.$router.push(`/edit-database/${this.checkURLParamsID()}`);
            },  
            async getDBName(id) {
                let dbData = await fetchDatabaseDetailsJSON(id);
                if(dbData.state) {
                    if(dbData.results.items.results.length) {
                        dbData = dbData.results.items.results[0];
                        let dbName = this.convertToCustomizationName(dbData.physical_file_path) ;
                        let data = await fetchDatabaseItemsListJSON(dbName, this.pageNumber, this.pageSize)
                        //  get by DB parent_id - waiting for POS
                        // let data = await fetchDatabaseItemsListJSON(this.pageNumber, this.pageSize, this.checkURLParamsID())

                        this.setupInsBaseTable();
                        if(data.results.items.results.length) {
                            this.updateTableData(data.results.items);
                        } else {
                            this.itemList = [];
                            this.hasData = false;
                        }     
                    } else {
                        this.invalidParamIDHandler("database");
                    }    
                } else {
                    this.invalidParamIDHandler("database");
                }        
            }, 
            async fetchDatabaseItemsList() {
				this.hasData = true;
                this.setInsBaseTableLoader(
					"Processing Your Data",
					"Just a moment, we're fetching and processing your data.",
					"processing"
				);   
                await this.getDBName(this.checkURLParamsID());                
            },
            async deleteItem(data, message) {
                let response;
                let formData = this.buildFormData(this.getName(data.name));
				let deleteItem = await deleteDatabaseItem(formData)
					.then(response => {
						this.deleteSuccessHandler(response, message);
					})
					.catch(error => {
						this.deleteErrorHandler(error);
					});
			},
			deleteSuccessHandler(response, message) {
				toastIt('success', `${message} been deleted.`, InsitesUtil.ops());
				this.getDatabaseList();
			},
			deleteErrorHandler(error) {
				console.error("Delete item Error: ", error);
				toastIt("error", "Failed to delete item. Please try again.", InsitesUtil.ops());
			},
			bulkDeleteHandler(selections) {
                let counter = 0;
				selections.forEach(data => {
					if (this.continueDeletion){
						this.deleteItem(data.rawData, "The selected items have");
						counter++;
						if (selections.length === counter) {
							this.continueDeletion = false;
							this.is_enabled = true;
						}
					}
				});
			},
			archiveItem(data) {
				swal({
					title: "",
					html: `
					<ins-loader
						state-icon="warning"
						icon-color="warning"
						state-message="Are you sure you want to delete this item?">
					</ins-loader>`,
					type: "",
					width: 330,
					showCancelButton: true,
					confirmButtonClass: "negative",
					confirmButtonText: "Delete"
				}).then(result => {
					if (result.value){
						this.deleteItem(data.rawData, "item has");
					}
				});
			},
			archiveSelection(selections) {
				swal({
					title: "",
					html: `
					<ins-loader
						state-icon="warning"
						icon-color="warning"
						state-message="Are you sure you want to delete these items?">
					</ins-loader>`,
					type: "",
					width: 330,
					showCancelButton: true,
					confirmButtonClass: "negative",
					confirmButtonText: "Delete"
				}).then(result => {
					if (result.value) {
					this.continueDeletion = true;
					this.bulkDeleteHandler(selections);
					}
				});
			},
            updateTableData(data) {
				let databaseList = data;
				this.$refs.insBaseTable.totalCount = databaseList.total_entries;
				this.$refs.insBaseTable.tableData = databaseList.results.map(this.constructTableData);                
				this.$refs.insBaseTable.loadingScreen = false;
				this.hasData = true;
            },
            async getTableHeader() {
                let data = await fetchDatabaseDetailsJSON(this.checkURLParamsID());
                if(data.state) {
                    data = data.results.items.results;
                    if(data.length) {
                        this.databaseName = data[0].name;
                        this.databasePhysicalPath = data[0].physical_file_path;
                        this.setBreadCrumbs();
                        data = data[0].database_fields;
                        for(let i in data) {
                            if (!data[i].metadata.is_header) {
                                data[i].metadata = {};
                            }
                            
                            if (data[i].metadata.is_header === "true" || data[i].metadata.is_header === true) {
                                if (data[i].metadata.label) {
                                    this.tableHeader = data[i].metadata.label;
                                } else {
                                    this.tableHeader = data[i].name;
                                }
                                this.tableHeaderName = data[i].name;
                            }
                        }
                    } else {
                        this.invalidParamIDHandler("database");
                    }
                } else {
                    this.invalidParamIDHandler("database");
                }              
            },
            constructTableData(item) {
                let table = {
					rawData: item,
                    id: item.id,                    
                    ID: item.id,
                    "Created Date": InsitesUtil.formatLocalisation(item.created_date)
                }                

                if(this.tableHeaderName) {
                    table[this.tableHeader] = item.properties[this.tableHeaderName];
                }
                return table;
			},
			setupInsBaseTable() {
				this.$refs.insBaseTable.bulkActions = ["Delete"];
				this.$refs.insBaseTable.rowActions = ["Edit", "Delete"]; 
				this.$refs.insBaseTable.tableHeaders = [
                    this.constructTableHeader("ID", "string", true),
                    this.constructTableHeader("Created Date", "string", true),
                ];
                
                if(this.tableHeader) {
                    this.$refs.insBaseTable.tableHeaders.splice(1, 0, this.constructTableHeader(this.tableHeader, "string", true));
                }
			},
            tableSortHandler(event) {
                let sortColumn = "";

                switch(event.detail.keyword) {
                    case "ID":
                        sortColumn = "id";
                        break;
                    case "Created Date":
                        sortColumn = "created_at";
                        break;
                    default:
                        sortColumn = `properties.${this.tableHeaderName}`;                    
                }

                this.sortedKeyword = sortColumn;
                this.sortedBy = event.detail.order;
                this.searchDatabase();
            },
			tablePaginationHandler(event) {
				this.$refs.insBaseTable.loadingScreen = true;
				this.pageSize = event.detail.pageSize;
				this.pageNumber = event.detail.pageNumber;
				this.fetchDatabaseItemsList();
			},
			tableSearchHandler(event) {
                // version 2.0.0  - no search yet
				if (event.detail.keyCode === 13 && event.detail.value !==  "") {
					this.searchKeyword = event.detail.value;
					this.pageNumber = 1;
                    this.$refs.insBaseTable.pageNumber = 1;
					this.searchDatabase();
				} else if(event.detail.value === "" && this.searchKeyword !== "") {
                    this.fetchDatabaseItemsList();
                }
            },
            async searchDatabase() {
                this.$refs.insBaseTable.loadingScreen = true;
                let searchField = `properties.${this.tableHeaderName}`;
                let data = 
                    !! this.tableHeader 
                    ? await searchDatabaseItemsListJSON(this.convertToCustomizationName(this.databasePhysicalPath),
                                                        this.pageNumber,
                                                        this.pageSize,
                                                        this.sortedKeyword,
                                                        this.sortedBy,
                                                        this.sortedKeyword,
                                                        this.searchKeyword)
                    : await searchDatabaseItemIDListJSON(this.convertToCustomizationName(this.databasePhysicalPath),
                                                        this.pageNumber,
                                                        this.pageSize,
                                                        this.sortedKeyword,
                                                        this.sortedBy,
                                                        this.searchKeyword);

                this.setupInsBaseTable();
                if(data.state) {
                    if(data.results.items.results.length) {
                        this.updateTableData(data.results.items);
                    } else {
                        this.itemList = [];
                        this.hasData = false;
                    }     
                } else {
                    console.log("Error search item", data.results);
                    toastIt("error", "Failed to search item. Please try again.", InsitesUtil.ops());
                }    
            },
			tableBulkActionHandler(event) {
				switch (event.detail.action) {
					case "Delete":
						this.archiveSelection(event.detail.selections);
					break;
				}
			},
			tableRowActionHandler(event) {
				let data = event.detail.data;
				let action = event.detail.action;
				switch (event.detail.action) {
					case 'Delete':
						    this.archiveItem(event.detail.data);
						break;

					case 'Edit':
						this.goToManageDatabaseItem(data, action);
						break;           
				}
            },
            setInsBaseTableLoader(title, message, icon){
				this.$refs.insBaseTable.loadingScreen = true;
				this.$refs.insBaseTable.loaderTitle = title;
				this.$refs.insBaseTable.loaderMessage = message
				this.$refs.insBaseTable.loaderIcon = icon;
			},
            setBreadCrumbs() {
                let crumbs = [];
                crumbs = [sidebarMenuDatabaseRoute, listDatabaseRoute,
                    {link: `#${this.$route.path}`, app: false, withSubmenu: false, label: `${this.databaseName}`}];
                InsitesUtil.setBreadCrumbs(crumbs);
            },
        },
        mounted() {
            this.getTableHeader();
            this.fetchDatabaseItemsList();
           
        }        
    }
</script>