<template>
  <div class="system-field-wrap">
    <h2>{{ heading }}</h2>
    <ins-base-table v-if="!loading"
      without-search static-table ref="systemFieldTable"
      v-on:onPaginationChange="onPaginationChangeHandler">
      <ins-base-table-row>
        <ins-base-table-td>
          <div class="new-system-field-wrap">
            <InsInputWrap v-model="newSystemField"
              v-bind:placeholder="`Please enter ${heading}`" />

            <ins-button label="Save" solid
              ref="newSystemFieldButton"
              v-bind:loading="addingSystemField"
              v-on:onClickInsButton="addSystemField">
            </ins-button>
          </div>

        </ins-base-table-td>
      </ins-base-table-row>

      <ins-base-table-row v-if="!loading" v-for="field in fields.results" v-bind:key="field.id">
        <ins-base-table-td class="system-field-wrap__data">

          <div class="view-mode" v-if="!field.editMode">
            {{ field.field_label }}
          </div>

          <div class="edit-mode" v-if="field.editMode">
            <InsInputWrap v-model="field.tempValue"
              v-bind:placeholder="`Please enter ${heading}`" />

            <div class="edit-mode__buttons">
              <ins-button label="update" size="small"
                v-on:onClickInsButton="updateSystemField(field)">
              </ins-button>

              <ins-button label="cancel" size="small"
                v-on:onClickInsButton="cancelEditMode(field)">
              </ins-button>
            </div>
          </div>

          <div class="actions-wrap" v-if="!field.editMode">
            <button class="action edit"
              v-on:click="enableEditMode(field)">
              Edit
            </button>
            <span class="seperator">|</span>
            <button class="action archive"
              v-on:click="archiveSystemField(field)">
              Delete
            </button>
          </div>

        </ins-base-table-td>
      </ins-base-table-row>

    </ins-base-table>
  </div>
</template>

<script>
import InsInputWrap from "../../InsInputWrap/InsInputWrap";
import { fetchDatabaseFieldGroupsJSON } from "../../../services/DatabaseServices.js"
import InsitesMixins from "../../../mixins/InsitesMixins.js";

export default {
  name: "SystemField",
  mixins: [InsitesMixins],
  components: {
    InsInputWrap
  },
  props: {
    heading: String,
    type: String
  },
  data() {
    return {
        fields: {
          results: []
        },
        loading: false,
        newSystemField: '',
        addingSystemField: false,
        pageNumber: 1,
        pageSize: 10,
        headers: {},
        sourceURL: '/api/customizations/',
        systemFieldModel: {
          form_configuration_name: "modules/insites_database/insites_manage_field_groups",
          parent_resource_id: "modules/insites_database/insites_field_groups",
          form: {
            properties: {
              field_label: '',
              is_enabled: 'true'
            }            
          }
        }
    }
  },
  methods: {
    enableEditMode(field){
      field.editMode = true;
    },
    cancelEditMode(field){
      field.tempValue = field.field_label;
      field.editMode = false;
    },
    mutateSystemField(method, field, callback) {
      this.systemFieldModel.form.properties.field_label = field.tempValue;
      
      let url = `${this.sourceURL}${method === 'put' ? field.id : ''}`;
      let body = JSON.stringify(this.systemFieldModel);
      let config = { headers: this.headers }
      this.$http[method](url, body, config)
        .then(callback).catch(this.errorHandler);
    },
    addSystemField(){
      if(this.newSystemField) {        
        this.addingSystemField = true;
        this.systemFieldModel.form.properties.is_enabled = true;
        let lowCase = this.newSystemField.toLowerCase();

        let field = { tempValue: this.newSystemField }
        this.mutateSystemField('post', field, () => {
            setTimeout(() => {
                toastIt("success", "New field group has been created.", InsitesUtil.ops());
                this.addingSystemField = false;
                this.newSystemField = "";
                this.fetchSystemFields();
                this.$store.dispatch('fetchSystemFields', this.type);
            }, 1000);
        });
      } else {
        toastIt('error', 'Cannot add empty field.', InsitesUtil.ops());
      }
    },
    updateSystemField(field){
      if(field.tempValue) { 
        this.mutateSystemField('put', field, res => {
          field.field_label = field.tempValue;
          field.editMode = false;
          setTimeout(() => {
            this.$store.dispatch('fetchSystemFields', this.type);
            toastIt('success', 'Field group has been updated.', InsitesUtil.ops());
          }, 1000)
        });
      } else {
        toastIt('error', 'Cannot add empty field.', InsitesUtil.ops());
      }
    },
    archiveSystemField(field){
      let deleteMsg = 'Are you sure you want to delete this Field Group?';
      swal({
        title: "",
        html: `
          <ins-loader
            state-icon="warning"
            icon-color="warning"
            state-message="${deleteMsg}">
          </ins-loader>`,
        type: "",
        width: 330,
        showCancelButton: true,
        confirmButtonClass: "negative",
        confirmButtonText: "Delete"
      }).then(result => {
        if (result.value) {    
          this.systemFieldModel.form.properties.is_enabled = false;
          this.mutateSystemField('put', field, () => {
            setTimeout(() => {
              toastIt("success", "Field group has been deleted.", InsitesUtil.ops());
              this.fetchSystemFields();
              this.$store.dispatch('fetchSystemFields', this.type);
            }, 1000);
          });
        }
      });
    },
    onPaginationChangeHandler(event){
      this.pageNumber = event.detail.pageNumber;
      this.pageSize = event.detail.pageSize;
      this.fetchSystemFields();
    },
    async fetchSystemFields(){
      this.loading = true;
      this.loaderStateHandler("", "Loading field groups...", "processing");
      let fieldsData = await fetchDatabaseFieldGroupsJSON(this.pageNumber, this.pageSize);
      if (fieldsData.state) {    
        fieldsData.results.items.results.map(this.constructResults);
        this.fields = fieldsData.results.items;
      } else {
        toastIt("error", "Failed to load field groups.", InsitesUtil.ops());
      }
      this.loading = false;
      this.setupTable();
    },
    constructResults(result){
      result.tempValue = result.field_label;
      result.editMode = false;
      return result;
    },
    errorHandler(error){
      console.error('error...', error);
      this.addingSystemField = false;
      toastIt('error', 'Failed to load field groups.', InsitesUtil.ops());
    },
    loaderStateHandler(title, message, type) {
      this.$refs.systemFieldTable.loadingScreen = true;
      this.$refs.systemFieldTable.loaderTitle = title;
      this.$refs.systemFieldTable.loaderMessage = message;
      this.$refs.systemFieldTable.loaderIcon = type;
    },
    setupTable(){
      setTimeout(() => {
        this.$refs.systemFieldTable.totalCount = this.fields.total_entries;
        this.$refs.systemFieldTable.pageNumber = this.pageNumber;
        this.$refs.systemFieldTable.pageSize = this.pageSize;
        this.$refs.systemFieldTable.loadingScreen = false;
      }, 300);
    },
  },
  mounted(){
    this.headers = this.apiHeader();
    this.fetchSystemFields();
  },
}
</script>

<style lang="scss" src="./SystemField.scss">
</style>