<template>
    <ins-datepicker  ref="insDatePickerEl"
        v-bind:label="label"
        v-bind:name="name"
        v-bind:min="min"
        v-bind:max="max"
        v-bind:value="value"
        v-bind:required="required"
        v-bind:has-error="hasError"
        v-bind:error-message="errorMessage"
        @onDateChange="onDateChange">
    </ins-datepicker>
</template>

<script>
export default {
    name: "InsDatePicker",
    props: {
        label: String,
        name: String,
        disabled: Boolean,
        min: String,
        max: String,
        value: String,
        required: Boolean,
        dateFormat: String,
        hasError: Boolean,
        errorMessage: String
    },
    methods: {
        onDateChange(event) {
            this.validateDate(event.detail.value);
            this.$emit("input", event.detail.value);
        }, 
        formatDate(date) {
            if(date || date !== "") {
                return moment(date).format('MM/DD/YYYY');
            }
        },
        validateDate(date){  
            if(this.required === true && date === "") {
                this.toggleError(true, `${this.name} is required.`); 
            } else {
                this.validateFormat(date);
            }
        },       
        validateFormat(date) {
            let date_regex = /^\d{2}\/\d{2}\/\d{4}$/;
            if(!date_regex.test(date) || this.formatDate(date).toLowerCase() === "invalid date") {
                this.toggleError(true, `Invalid date format. (${this.dateFormat})`);
            } else {   
                this.toggleError(false, `${this.name} is required.`);  
                this.$emit('input', date);  
            }         
            if(this.required !== true && date === "") {                
                this.toggleError(false, `${this.name} is required.`);
            } 
        },
        toggleError(error, message){      
            this.$refs.insDatePickerEl.hasError = error ? true : false;
            this.$refs.insDatePickerEl.errorMessage = message;

            this.$emit("fieldError", {
                error: error ? true : false,
                target: this
            });
        }
    }
}
</script>

