<template>
  <div class="grid-container-full pages">
    <div class="grid-x">
      <div class="large-12 cell manage-database-container">
        <!-- database filters -->
        <div class="database-filter"></div>

        <ins-card steady class="height-auto">
          <div class="page-addnew-top">
            <h2 class="h2desc">Database Details</h2>
          </div>
		  <div class="row-margin-x">
			<div class="grid-x grid-padding-x">
				<div class="large-5 cell database-details-form">
				<InsInputWrap 
					field="text"
					required="'true'"
					placeholder="Database Name"
					label="Database Name*"
					maxlength="50"
					v-model="databaseName"
					errorMessage="Database Name is required."  />
				</div>
				<div class="large-7 cell database-details-form">
					<div class="database-column-name">
						<label class="liquid-label">Customization Model Name</label>
						<pre class="liquid-wrap">{{ setCustomizationModelName(databasePhysicalFilePath) }}</pre>
					</div>
				</div>
			</div>
          </div>
		</ins-card>
          <div class="spacer"></div>
		<ins-card steady>
          <div class="no-margin-bottom">
              <h2 class="h2desc">Fields</h2>
          </div>

          <div class="fields">
			<div class="grid-x grid-padding-y">
				<div class="large-6 small-12 cell">					
					<ins-button outlined color="blue" label="Add Field" v-on:click="resetDatabaseField" v-bind:disabled="hasProcessingRequest"></ins-button>
				</div>
				<div class="large-6 small-12 cell unpublished-changes align-right vertical-align-middle">
					<p class="p-warning" v-if="hasUnpublishedChanges"><span class="icon-warning"></span> You have unsaved changes.</p>
					<p class="p-warning" v-if="fieldTableHeaderCounter < 0 && fields.length">No table header is set.</p>
					<p class="p-error" v-if="hasExistingFields"><span class="icon-error"></span>  Database has duplicate field names.</p>
				</div>
			</div>
            <div class="grid-x">
              <div class="medium-4 cell">
				<InsDropdownList
					ref="insDropdownListEl"
					v-bind:options="setDropdownListOptions()"
					v-bind:header="fieldTableHeaderCounter"
					v-on:selectChange="selectDatabaseField"
					></InsDropdownList>
				<div class="sort-buttons">
					<ins-button solid color="blue" icon="icon-caret-up" label="" v-bind:disabled="!fieldProperties.name || hasProcessingRequest" v-on:click="sortFieldUp"></ins-button>
					<ins-button solid color="blue" icon="icon-caret-down" label="" v-bind:disabled="!fieldProperties.name || hasProcessingRequest" v-on:click="sortFieldDown"></ins-button>
					<!--<button v-on:click="sortFieldUp" :disabled="!fieldProperties.name" class="btn-sort"><span class="icon-caret-up"></span></button>
					<button v-on:click="sortFieldDown" :disabled="!fieldProperties.name" class="btn-sort"><span class="icon-caret-down"></span></button> -->
			  	</div>
			  </div>
              <div class="medium-8 cell dropdown-list-fields">
			  <!--<p v-if="fieldName">Weight: {{ fieldProperties.metadata.weight }} </p>-->
                <div class="grid-x">
                  <div class="cell large-12 medium-12 small-12" v-if="hasFieldPropertiesData">
					<div class="dropdown-list-fields-form row-margin-x grid-x grid-padding-x">
						<div class="large-6 cell">
							<InsInputWrap 
								field="text"
								class="database-field-name"
								v-model="fieldName"
								v-on:input="checkFieldName"
								maxlength="50"
								placeholder="Field Name"
								label="Field Name*"
								required="'true'"
								:errorMessage="fieldNameErrorMessage" />
						</div>
						<div class="large-6 cell">
							<div class="database-column-name">
								<label class="liquid-label">Database Column Name</label>
								<pre class="liquid-wrap">{{ setDatabaseColumnName(fieldName) }}</pre>
							</div>
						</div>
						<div class="large-6 cell">
							<InsSelectWrap
								class="large-12 medium-12 small-12 cell"
								:label="fieldProperties.id ? `Field Type` : `Field Type*`"
								placeholder="- Select -"
								v-model="fieldType"
								v-bind:required="true"
								v-on:input="fieldTypeInputHandler"
								errorMessage="Field Type is required."
								ref="fieldTypeWrap"
								searchable>
								<ins-select-option
									v-if="fieldTypes.length"
									v-for="item in getFieldTypes()"
									v-bind:key="item.id"
									v-bind:label="item.name"
									v-bind:value="item.id"
								></ins-select-option>
							</InsSelectWrap>
						</div>
						<div 
							v-if="fieldMetadata.has_metadata_value"
							class="large-6 cell position-relative">
							<InsInputWrap 
								field="text"
								class="inline-b"
								v-model="fieldMetadataValue"
								placeholder="Options"
								label="Options* (separated by comma)"
								required="'true'"
								errorMessage="Options is required." />								
							<i class="ins-tooltip position-absolute" tooltip-content="Column marked as 'table header' will be displayed on item list table."></i>
						</div>
					</div>
					<div class="grid-x">
						<InsSelectWrap
							class="large-12 medium-12 small-12 cell"
							label="Field Group"
							placeholder="- Select -"
							dynamic-placeholder="Add New Field Group"
							v-if="mutatedGroups"
							v-model="fieldGroup"
							searchable
							with-dynamic-option
							v-on:onSubmitOption="onSubmitOptionHandler">
							<ins-select-option
								v-if="!fieldGroups.length"
								label="No field group yet."
								value
								disabled
							></ins-select-option>
							<ins-select-option
								v-if="fieldGroups.length && fieldProperties.name"
								label="None"
								value=""
							></ins-select-option>
							<ins-select-option
								v-for="item in fieldGroups"
								v-bind:key="item.id"
								v-bind:label="item.field_label"
								v-bind:value="item.id"
							></ins-select-option>
						</InsSelectWrap>
					</div>
					<div class="row-margin-x">
					  <div class="grid-x grid-padding-x">
						<div class="large-6 cell">
							<InsToggleWrap
								v-bind:checked="fieldTableHeader"
								v-model="fieldTableHeader"
								class="inline-block"
								label="Table Header" />
							<i class="ins-tooltip" tooltip-content="Column marked as 'table header' will be displayed on item list table."></i>
						</div>
						<div class="large-6 cell"
							v-if="fieldMetadata.has_mandatory">
							<InsToggleWrap
								v-bind:checked="fieldMandatory"
								v-model="fieldMandatory"
								label="Mandatory" />
						</div>
					  </div>
					</div>

					<div class="grid-x grid-padding-y">
						<div class="large-6 medium-5 small-12 cell footer-btn-container">
							<ins-button color="negative" label="Remove Field" v-on:click="removeDatabaseField(fieldProperties.counter)" v-if="!fieldProperties.id && fieldProperties.counter > -1"></ins-button>
						</div>
						<div class="large-5 large-offset-1 medium-5 medium-offset-2 small-12 cell text-right footer-btn-container">
							<ins-button solid color="blue" label="Save Field" v-bind:disabled="hasProcessingRequest" v-on:click="saveDatabaseField"></ins-button>
						</div>
					</div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </ins-card>
		<div class="grid-x grid-padding-y">
			<div class="large-4 medium-5 small-12 cell footer-btn-container">
				<ins-button color="negative" label="Delete Database" v-on:click="onDeleteHandler"  v-bind:disabled="hasProcessingRequest" v-if="$route.name === 'EditDatabase'"></ins-button>
			</div>
			<div class="large-4 large-offset-4 medium-5 medium-offset-2 small-12 cell text-right footer-btn-container">
				<ins-button outlined color="blue" v-on:click="onCancelHandler"  v-bind:disabled="hasProcessingRequest" label="Cancel"></ins-button>
				<ins-button solid color="blue" v-on:click="onSubmitHandler" v-bind:disabled="hasExistingFields || hasProcessingRequest" :label="$route.name === 'EditDatabase' ? 'Update' : 'Save'"></ins-button>
				<!-- :disabled="fieldTableHeaderCounter > -1 ? 'false' : 'true'" -->
			</div>
		</div>
      </div>
    </div>
  </div>
</template>

<script>
	import { mapState } from "vuex";
	import { isEqualWith, uniqBy, sortBy } from "lodash";

	import InsInputWrap from "../../components/InsInputWrap/InsInputWrap.vue"
	import InsToggleWrap from "../../components/InsToggleWrap/InsToggleWrap.vue"
	import InsSelectWrap from "../../components/InsSelectWrap/InsSelectWrap.vue";
	import InsDropdownList from "../../components/InsDropdownList/InsDropdownList.vue";

	import InsitesMixins from "../../mixins/InsitesMixins.js";
	import DatabaseManagerMixins from "../../mixins/DatabaseManagerMixins.js";
	import DatabaseItemsMixins from "../../mixins/DatabaseItemsMixins.js";
	import DatabaseDeleteMixins from "../../mixins/DatabaseDeleteMixins.js";

	import { fetchDatabaseListJSON,
			 fetchDatabaseFieldGroupsJSON,
			 fetchDatabaseItemsListJSON,
			 fetchDatabaseDetailsJSON,
			 fetchFormConfigurationListJSON,
			 addGroup,
			 createDatabase,
			 updateDatabase,
			 truncateDatabase,
			 deleteDatabase,
			 createFormConfiguration,
			 updateFormConfiguration } from "../../services/DatabaseServices";

	import { sidebarMenuDatabaseRoute, listDatabaseRoute, addNewDatabaseRoute } from "../../models/InsDatabaseRoutes.js";


	export default {
		data() {
			return {
				groupModel: {
					form_configuration_name: "modules/insites_database/insites_manage_field_groups",
					parent_resource_id: "modules/insites_database/insites_field_groups",
					form: {
						properties: {
							field_label: '',
							is_enabled: 'true'
						}            
					}
				},
				mutatedGroups: false,
				oldFields: {},
				oldFieldTableHeaderCounter: -1,
				databaseName: "",
				databasePhysicalFilePath: "",
				customizationModelName: "",
				hasProcessingRequest: false,
				hasUnpublishedChanges: false,
				hasExistingFields: false,
				isExisting: false,
				fieldNameErrorMessage: "Field Name is required.",
				isPublished: true,
				hasFieldPropertiesData: false,
				fields: [],
				selectedField: "",
				fieldTableHeaderCounter: -1,
				fieldTableHeader: false,
				fieldName: "",
				fieldType: "",
				fieldMetadataValue: "",
				fieldGroup: "",
				fieldMetadata: {},
				fieldMandatory: false,
				deprecatedAttributeTypes: [
					{
						attribute_type: "array",
						new_value: "string"
					},
					{
						attribute_type: "text",
						new_value: "string"
					}
				],
				fieldTypes: [
					{
						id: "text",
						name: "Text",
						attribute_type: "string",
						has_metadata_value: false,
						has_mandatory: true
					},
					{
						id: "link",
						name: "Link",
						attribute_type: "string",
						has_metadata_value: false,
						has_mandatory: true
					},
					{
						id: "multi_line",
						name: "Multi-line",
						attribute_type: "string",
						has_metadata_value: false,
						has_mandatory: true
					},
					{
						id: "list_select",
						name: "List (Select)",
						attribute_type: "string",
						has_metadata_value: true,
						has_mandatory: true
					},
					{
						id: "list_checkbox",
						name: "List (Checkbox)",
						attribute_type: "string",
						has_metadata_value: true,
						has_mandatory: true
					},
					{
						id: "list_radio",
						name: "List (Radio)",
						attribute_type: "string",
						has_metadata_value: true,
						has_mandatory: true
					},
					{
						id: "toggle",
						name: "Toggle",
						attribute_type: "boolean",
						has_metadata_value: false,
						has_mandatory: false
					},
					{
						id: "number",
						name: "Number",
						attribute_type: "integer",
						has_metadata_value: false,
						has_mandatory: true
					},
					{
						id: "decimal",
						name: "Decimal",
						attribute_type: "float",
						has_metadata_value: false,
						has_mandatory: true
					},
					{
						id: "date",
						name: "Date",
						attribute_type: "date",
						has_metadata_value: false,
						has_mandatory: true
					}
				],
				fieldProperties: {},
				selectedGroup: ""
			};
		},
		components: {
			InsDropdownList,
			InsSelectWrap,
			InsInputWrap,
			InsToggleWrap
		},
		computed: mapState({
			databaseFormData: state => state.DatabaseStore.databaseFormData,
			fieldGroups: state => state.DatabaseStore.fieldGroups
		}),
		mixins: [InsitesMixins, DatabaseManagerMixins, DatabaseItemsMixins, DatabaseDeleteMixins],
		methods: {
			sortFieldUp() {
				let sortedFields = this.setDropdownListOptions();
				let sortedWeight = this.fieldProperties.metadata.weight - 1;

				if (sortedWeight >= 0) {
					this.fields[sortedFields[this.fieldProperties.metadata.weight - 1].counter].metadata.weight = this.fieldProperties.metadata.weight;
					this.fields[this.fieldProperties.counter].metadata.weight = sortedWeight;
					this.setUnpublishedChanges();
				}
				this.fieldProperties = this.fields[this.fieldProperties.counter];
			},
			sortFieldDown() {
				let sortedFields = this.setDropdownListOptions();
				let sortedWeight = this.fieldProperties.metadata.weight + 1;
				let maxSortedWeight = sortedFields[sortedFields.length - 1].metadata.weight;

				if (sortedWeight <= maxSortedWeight) {
					this.fields[sortedFields[this.fieldProperties.metadata.weight + 1].counter].metadata.weight = this.fieldProperties.metadata.weight;
					this.fields[this.fieldProperties.counter].metadata.weight = sortedWeight;
					this.setUnpublishedChanges();
				}
				this.fieldProperties = this.fields[this.fieldProperties.counter];
			},
			setDropdownListOptions() {
				let sortedFields = sortBy(this.fields, [
					function(field) {
						return field.metadata.weight; 
					}
				]);

				return sortedFields;
			},
			async onSubmitOptionHandler(event) {
				this.groupModel.form.properties.field_label = event;
				this.mutatedGroups = false;
				let response = await addGroup(this.groupModel);
				this.mutatedGroups = true;

				if (response.state) {
					this.fieldGroups.push({
						id: response.results.id,
						field_label: event
					});
					
					this.fieldGroup = response.results.id;
					this.onSuccessRequestState("New field group has been created.");
				} else {
					this.onErrorRequestState("group");
				}
			},
			setCustomizationModelName(name) {
				if (this.$route.name === "EditDatabase") {
					this.customizationModelName = this.convertToCustomizationName(this.databasePhysicalFilePath);
				} else {
					this.customizationModelName = this.convertToCustomizationName(this.setFormConfigurationName(this.databaseName));
				}

				return this.customizationModelName;
			},
			setDatabaseColumnName(name) {
				if (!this.fieldProperties.id) {
					let module_name = this.convertToModuleName(name)
					return module_name ? module_name : ' ';
				} else {
					return this.convertToModuleName(this.fieldProperties.name);
				}
			},
			onDeleteHandler() {
				let self = this;

				swal({
					title: "",
					html: `
					<ins-loader
						state-icon="warning"
						icon-color="warning"
						state-message="Are you sure you want to delete this database? \n This will also delete existing data.">
					</ins-loader>`,
					type: "",
					width: 330,
					showCancelButton: true,
					cancelButtonText: "Cancel",
					confirmButtonClass: "negative",
					confirmButtonText: "Delete"
				}).then(result => {
					if (result.value) {
						self.processDeleteDatabase();
					}
				});
			},
			async processDeleteDatabase() {
				let response = await this.deleteHandler(this.checkURLParamsID(), this.convertToCustomizationName(this.databasePhysicalFilePath));

				if (response.state) {
					this.onSuccessRequestState("Database deleted.", true);
				} else {
					this.onErrorRequestState("database");
				}
			},
			// async deleteHandler() {
			// 	let response = await fetchDatabaseItemsListJSON(this.convertToCustomizationName(this.databasePhysicalFilePath));

			// 	if (response.state) {
			// 		if (response.results.items.results.length) {
			// 			this.truncateDatabaseHandler();
			// 		} else {
			// 			this.deleteDatabaseHandler();
			// 		}
			// 	} else {
			// 		this.onErrorRequestState("database");
			// 	}
			// },
			// async truncateDatabaseHandler() {
			// 	let response = await truncateDatabase({ name: this.convertToCustomizationName(this.databasePhysicalFilePath) });
				
			// 	if (response.state) {
			// 		this.deleteDatabaseHandler();
			// 	} else {
			// 		this.onErrorRequestState("database");
			// 	}
			// },
			// async deleteDatabaseHandler() {
			// 	let response = await deleteDatabase({ id: this.checkURLParamsID() });

			// 	if (response.state) {
			// 		this.onSuccessRequestState("Database deleted.", true);
			// 	} else {
			// 		this.onErrorRequestState("database");
			// 	}
			// },
			getFieldTypes() {
				let filteredFieldTypes = [];

				if (this.fieldProperties.id) {
					filteredFieldTypes = this.getFilteredFieldTypes(this.getDeprecatedAttributeTypesNewValue(this.fieldProperties.attribute_type));
					
					if (!this.fieldProperties.metadata.type) {
						this.fieldType = filteredFieldTypes[0].id;
					}
				} else {
					filteredFieldTypes = this.fieldTypes;
				}

				return filteredFieldTypes;
			},
			getFilteredFieldTypes(attribute_type) {
				let filteredFieldTypes = [];

				for (let counter = 0; counter < this.fieldTypes.length; counter++) {
					if (this.fieldTypes[counter].attribute_type === attribute_type) {
						filteredFieldTypes.push(this.fieldTypes[counter]);
					}
				}

				return filteredFieldTypes;
			},
			getDeprecatedAttributeTypesNewValue(attribute_type) {
				for (let counter = 0; counter < this.deprecatedAttributeTypes.length; counter++) {
					if (attribute_type === this.deprecatedAttributeTypes[counter].attribute_type) {
						return this.deprecatedAttributeTypes[counter].new_value;
					}
				}

				return attribute_type;
			},
			checkFieldName() {
				let isExisting = false;

				for (let counter = 0; counter < this.fields.length; counter++) {
					if (this.convertToModuleName(this.fieldName) === this.fields[counter].name && !this.fieldProperties.id) {
						isExisting = true;
						InsitesUtil.invalidFieldNotif($('.database-field-name').find('input')[0]);
						this.fieldNameErrorMessage = "Field Name already exist."
						break;
					}
				}

				if (!isExisting) {
					InsitesUtil.validFieldNotif($('.database-field-name').find('input')[0]);
					this.fieldNameErrorMessage = "Field Name is required."
				}
			},
			checkDuplicateFields() {
				let uniqueFields = uniqBy(this.fields, "name");

				this.hasExistingFields = uniqueFields.length !== this.fields.length;
			},
			removeDatabaseField(counter) {
				this.fields.splice(counter, 1);
				this.resetDatabaseField();
				this.setUnpublishedChanges();
			},
			saveDatabaseField() {
				let _form = $(".dropdown-list-fields-form");
				let _fields = Array.from(_form.find((".ins-form-field, .ins-select-value-input, .ins-textarea-field")));
				let isValid = this.validateDatabaseFields(_fields);
				
				if (isValid) {
					this.updateDatabaseFields();
					this.resetDatabaseField();
					this.setUnpublishedChanges();
				}
			},
			updateDatabaseFields() {
				this.fieldMetadata.is_required = this.fieldMandatory;

				if (this.fieldProperties.name) {
					this.setTableHeaderCounter(this.fieldProperties.counter);
					this.fields[this.fieldProperties.counter] = this.setFieldMetadata(this.setDatabaseFieldData(), this.fieldProperties.counter, this.fieldProperties.id, this.fieldProperties.metadata.weight);
				} else {
					this.setTableHeaderCounter(this.fields.length);
					this.fields.push(this.setFieldMetadata(this.setDatabaseFieldData(), this.fields.length));
				}
			},
			setTableHeaderCounter(counter) {
				if (this.fieldTableHeader) {
					this.fieldTableHeaderCounter = counter;
				} else if (this.fieldTableHeaderCounter === counter && !this.fieldTableHeader) {
					this.fieldTableHeaderCounter = -1;
				}

				this.$refs.insDropdownListEl.setHeader(this.fieldTableHeaderCounter);
			},
			setUnpublishedChanges() {
				if (this.$route.name === "EditDatabase") {
					if (this.oldFieldTableHeaderCounter === this.fieldTableHeaderCounter && isEqualWith(JSON.parse(this.oldFields), JSON.parse(JSON.stringify(this.fields)))) {
						this.hasUnpublishedChanges = false;
					} else {
						this.hasUnpublishedChanges = true;
					}
				}
			},
			setDatabaseFieldData() {
				return {
					label: InsitesUtil.removeWhitespace(this.fieldName),
					name: this.fieldProperties.id ? this.fieldProperties.name : this.convertToModuleName(this.fieldName),
					group_id: this.fieldGroup,
					metadata: this.fieldMetadata,
					values: this.fieldMetadataValue,
					mandatory: this.fieldMandatory
				}
			},
			validateDatabaseFields(_fields) {
				_fields.forEach((_field) => {
					if (_field.className === "ins-select-value-input") {
						let _fieldParent = _field.parentElement.parentElement.parentElement;
						if(_fieldParent.getAttribute('required') === "true") {
							_field.setAttribute("required", true);
							this.$refs.fieldTypeWrap.checkInput(_field.value);
						}
					}
				});
				
				return InsitesUtil.formValidate(_fields);
			},
			selectDatabaseField(field) {
				this.getFieldTypes();
				this.hasFieldPropertiesData = false;
				this.fieldProperties = field;
				this.fieldName = field.label ? field.label : field.name;
				this.fieldType = field.metadata.type ? field.metadata.type : this.fieldTypes[0].id;
				this.fieldGroup = field.metadata.group_id;
				this.fieldMandatory = field.metadata.is_required === "true";
				this.fieldTableHeader = field.counter === this.fieldTableHeaderCounter;
				this.fieldMetadataValue = field.metadata.values.length ? field.metadata.values.join(",") : "";
				this.fieldTypeInputHandler(this.fieldType);
				this.$refs.insDropdownListEl.setHeader(this.fieldTableHeaderCounter);

				setTimeout(() => {
					this.hasFieldPropertiesData = true;
				});
			},
			async initManageDatabase() {
				let response = await fetchDatabaseFieldGroupsJSON();
				this.mutatedGroups = true;

				if (response.state) {
					this.$store.commit("updateDatabaseFieldGroups", response.results.items.results);

					if (this.$route.name === "EditDatabase") {
						this.checkDatabaseFormData();
					} else if (this.$route.name === "ManageDatabase") {
						this.$store.commit("resetDatabaseFormData");
					}
				} else {
					this.onErrorRequestState("database", true);
				}
			},
			checkDatabaseFormData() {
				if (this.checkURLParamsID()) {
					if (this.databaseFormData.data) {
						this.setDatabaseFieldsValue();
						this.setDatabaseFields(
							this.databaseFormData.data.rawData.database_fields
						);

						this.checkDuplicateFields();
					} else {
						this.updateDatabaseFormData();
					}
				} else {
					// error
					this.onErrorRequestState("database", true);
				}
			},
			setDatabaseFieldsValue() {
				this.databaseName = this.databaseFormData.data.Name;
				this.databasePhysicalFilePath = this.databaseFormData.data.rawData.physical_file_path;
				this.setBreadCrumbs();
			},
			async updateDatabaseFormData() {
				let response = await fetchDatabaseDetailsJSON(this.checkURLParamsID());
					
				if (response.state) {
					this.$store.commit("updateDatabaseFormData", this.setDatabaseRawData(response.results.items.results[0], "Edit"));
			
					this.setDatabaseFields(response.results.items.results[0].database_fields);
					this.setDatabaseFieldsValue();
					this.checkDuplicateFields();
				} else {
					// error
					this.onErrorRequestState("database", true);
				}
			},
			setDatabaseRawData(result, action) {
				if (!result.metadata) {
					result.metadata = {};
				}

				return {
					action: action,
					data: {
						Name: result.metadata.label ? result.metadata.label : result.name,
						id: result.id,
						rawData: result
					}
				};
			},
			setDatabaseFields(databaseFields) {
				let newDatabaseFields = [];
				
				for (let counter = 0; counter < databaseFields.length; counter++) {
					if (!databaseFields[counter].metadata) {
						databaseFields[counter].metadata = {};
					}

					newDatabaseFields[counter] = {};
					newDatabaseFields[counter].attribute_type = this.getDeprecatedAttributeTypesNewValue(databaseFields[counter].attribute_type);
					newDatabaseFields[counter].counter = counter;
					newDatabaseFields[counter].default_value = null;
					newDatabaseFields[counter].id = databaseFields[counter].id;
					newDatabaseFields[counter].name = this.convertToModuleName(databaseFields[counter].name);
					newDatabaseFields[counter].label = databaseFields[counter].metadata.label ? databaseFields[counter].metadata.label : databaseFields[counter].name;
					newDatabaseFields[counter].is_required = databaseFields[counter].metadata.is_required ? databaseFields[counter].metadata.is_required : "false";
					
					if (databaseFields[counter].metadata.is_header === "true") {
						this.oldFieldTableHeaderCounter = counter;
						this.fieldTableHeaderCounter = counter;
						this.$refs.insDropdownListEl.setHeader(counter);
					}

					newDatabaseFields[counter].metadata = {};
					newDatabaseFields[counter].metadata = this.setDatabaseFieldMetadata(databaseFields[counter], counter);
					newDatabaseFields[counter].metadata.is_header = "false";
				}

				this.fields = newDatabaseFields;
				this.oldFields = JSON.stringify(newDatabaseFields);
			},
			setDatabaseFieldMetadata(data, counter) {
				if (!data.metadata.values) {
					data.metadata.values = [];
				}

				return {
                    "label": data.metadata.label ? data.metadata.label : data.name,
                    "group_id": data.metadata.group_id,
                    "type": data.metadata.type && data.id ? data.metadata.type : this.setDatabaseFieldMetadataType(data.attribute_type),
                    "is_required": data.metadata.is_required ? data.metadata.is_required : "false",
                    "is_enabled": "true",
                    "status": "enabled",
                    "values": data.metadata.values.length ? uniqBy(data.metadata.values) : [],
                    "weight": typeof data.metadata.weight !== "undefined" ? data.metadata.weight : counter
				};
			},
			setDatabaseFieldMetadataType(attribute_type) {
				let filteredFieldTypes = this.getFilteredFieldTypes(this.getDeprecatedAttributeTypesNewValue(attribute_type));

				if (filteredFieldTypes.length) {
					return filteredFieldTypes[0].id;
				}

				return false;
			},
			onSubmitHandler() {
				let _form = $(".database-details-form");
				let _fields = Array.from(_form.find((".ins-form-field, .ins-select-value-input, .ins-textarea-field")));
				let isValid = InsitesUtil.formValidate(_fields);
				
				if (isValid) {
					// this.checkDatabaseFormConfiguration();
					this.saveDatabase();
				}
			},
			async checkDatabaseFormConfiguration() {
				let response = await fetchFormConfigurationListJSON(this.setFormConfigurationName(this.databaseName));

				if (response.state) {
					if (response.results.items.results.length) {
						// update form configuration
						this.updateDatabaseFormConfiguration(response.results.items.results[0].id);
					} else {
						// create form configuration
						this.createDatabaseFormConfiguration();
					}
				} else {
					this.onErrorRequestState("form configuration");
				}
			},
			async createDatabaseFormConfiguration() {
				this.hasProcessingRequest = true;
				let response = await createFormConfiguration(this.setDatabaseFormConfigurationPayload());

				if (response.state) {
					this.saveDatabase();
				} else {
					this.onErrorRequestState("form configuration");
				}
			},
			async updateDatabaseFormConfiguration(id) {
				this.hasProcessingRequest = true;
				let response = await updateFormConfiguration(this.setDatabaseFormConfigurationPayload(id));

				if (response.state) {
					this.saveDatabase();
				} else {
					this.onErrorRequestState("form configuration");
				}
			},
			setDatabasePayload() {
				let data = this.buildDatabaseSchema(
					this.databaseName,
					this.customizationModelName,
					this.fields,
					this.fieldTableHeaderCounter,
					this.databasePhysicalFilePath
				);

				return data;
			},
			setDatabaseFormConfigurationPayload(id) {
				let data = this.buildFormConfigurationSchema(
					this.customizationModelName,
					this.databaseName,
					this.setFormConfigurationPhysicalPath(this.databaseName),
					this.fields
				);

				if (id) {
					data.id = id;
				}

				return data;
			},
			onCancelHandler() {
				let self = this;

				if (self.hasUnpublishedChanges) {
					swal({
						title: "",
						html: `
						<ins-loader
							state-icon="warning"
							icon-color="warning"
							state-message="You have unsaved changes.\n Would you like to continue and discard changes?">
						</ins-loader>`,
						type: "",
						width: 330,
						showCancelButton: true,
						cancelButtonText: "No",
						confirmButtonText: "Yes"
					}).then(result => {
						if (result.value) {
							self.$router.push({ name: "ListDatabase" });
						}
					});
				} else {
					self.$router.push({ name: "ListDatabase" });
				}
			},
			async saveDatabase() {
				let data = this.setDatabasePayload();
				let response = {};
				this.hasProcessingRequest = true;
				
				if (this.$route.name === "EditDatabase") {
					data.payload.id = this.checkURLParamsID();
					response = await updateDatabase(data);
				} else if (this.$route.name === "ManageDatabase") {
					response = await createDatabase(data);
				}

				this.onSuccessSaveDatabase(response);
			},
			onSuccessRequestState(message, redirect = false) {
				toastIt("success", message, InsitesUtil.ops());
				this.hasProcessingRequest = false;

				if (redirect) {
					this.$router.push({ name: "ListDatabase" });
				}
			},
			onErrorRequestState(label, redirect = false) {
				this.toastInvalidIDNotification(label);
				this.hasProcessingRequest = false;

				if (redirect) {
					this.$router.push({ name: "ListDatabase" });
				}
			},
			onSuccessSaveDatabase(response) {
				if (response.state) {
					this.onSuccessRequestState(`Database has been ${this.$route.name === "EditDatabase" ? "updated" : "created"}.`, true);
				} else {
					this.onErrorRequestState("database");
				}
			},
			resetDatabaseField() {
				this.hasFieldPropertiesData = false;
				this.fieldProperties = {};
				this.fieldName = "";
				this.fieldType = "";
				this.fieldGroup = "";
				this.fieldMetadataValue = "";
				this.fieldMetadata = {};
				this.fieldMandatory = false;
				this.fieldTableHeader = false;
				this.$refs.insDropdownListEl.resetSelectedHandler();
				
				setTimeout(() => {
					this.hasFieldPropertiesData = true;
				});
			},
			fieldTypeInputHandler(id) {
				for (let counter = 0; counter < this.fieldTypes.length; counter++) {
					if (id === this.fieldTypes[counter].id) {
						this.fieldMetadata = this.fieldTypes[counter];
						break;
					}
				}

				if (!this.fieldMetadata.has_mandatory) {
					this.fieldMandatory = false;
				}
			},
			setBreadCrumbs() {
				let crumbs = [];
				crumbs = [sidebarMenuDatabaseRoute, listDatabaseRoute];
				if (this.$route.name === "EditDatabase") { 
						crumbs.push({
						label: "Edit Database", 
						link: `#${this.$route.path}`, 
						withSubmenu: false, 
						app: false
					});
				} else if (this.$route.name === "ManageDatabase") {
					crumbs.push(addNewDatabaseRoute);				
				}
				InsitesUtil.setBreadCrumbs(crumbs);
			}
		},
		mounted() {
			this.initManageDatabase();
			this.resetDatabaseField();
			this.setBreadCrumbs();
		},
		beforeDestroy() {
			this.$store.commit("resetDatabaseFormData");
		}
		// beforeRouteLeave(to, from, next) {
		// 	if (this.hasUnpublishedChanges) {
		// 		const answer = window.confirm('You have unsaved changes.\n Would you like to continue and discard changes?');
				
		// 		if (answer) {
		// 			next();
		// 		} else {
		// 			console.log(this.fields);
		// 			next(false);
		// 		}
		// 	} else {
		// 		next();
		// 	}
		// }
	};
</script>

