const api_url = "/insites/api/";
const customization_url = "/api/customizations";
import { processRequestAndResponse, processRequest } from "./CoreServices.js";

// return database list
export async function fetchDatabaseListJSON(page = 1, size = 10, sort, order, keyword) {
    let url = `${api_url}get-database-list-data.json?page=${page}&size=${size}${sort ? `&sort=${sort}` : ''}${order ? `&order=${order}` : ''}${keyword ? `&keyword=${keyword}` : ''}`;
    return await processRequestAndResponse(url, 'get');
}

// return database details
export async function fetchDatabaseDetailsJSON(id, page = 1, size = 10) {
    let url = `${api_url}get-database-list-data.json?id=${id}&page=${page}&size=${size}`;
    return await processRequestAndResponse(url, 'get');
}

