// POST create database
export async function createDatabase(data) {
    let url = `${api_url}create-database.json`;
    return await processRequestAndResponse(url, 'post', data);
}

// POST update database
export async function updateDatabase(data) {
    let url = `${api_url}update-database.json`;
    return await processRequestAndResponse(url, 'post', data);
}

// POST delete database
export async function deleteDatabase(id) {
    let url = `${api_url}delete-database.json`;
    return await processRequestAndResponse(url, 'post', id);
}

