const DatabaseStore = {
	state: {
		fieldGroups: [],
		databaseFormData: {}
	},
	mutations: { 
		updateDatabaseFieldGroups(state, new_state) {
			state.fieldGroups = {};
			state.fieldGroups = new_state;
		},
		updateDatabaseFormData(state, new_state) {
			state.databaseFormData = {};
			state.databaseFormData = new_state;
		},
		resetDatabaseFormData(state) {
			state.databaseFormData = {};
		} 
	}
}

export default DatabaseStore;