<template>
	<div class="ins-dropdown-list-container">
		<ul>
			<li v-for="item in options"
				:class="setActiveHeader(item.counter)"
				v-on:click="selectedHandler(item)">{{ item.label }}<span v-if="item.is_required === 'true' | item.is_required === true"> *</span></li>
		</ul>
	</div>
</template>

<script>
	import Validators from '../../mixins/Validators';
	import InsitesMixins from '../../mixins/InsitesMixins';

	export default {
		name: "InsDropdownList",
		data() {
			return {
				selected: "",
				header: ""
			}
		},
		mixins: [Validators, InsitesMixins],
		props: {
			options: Array
		},
		methods: {
			setActiveHeader(counter) {
				let status = "";
				if (this.selected === counter) {
					status += "is-active";
				}

				if (this.header === counter) {
					status += status ? " is-header" : "is-header";
				}

				return status;
			},
			resetSelectedHandler() {
				this.selected = "";
			},
			selectedHandler(option) {
				this.selected = option.counter;
				this.$emit('selectChange', option);
			},
			setHeader(counter) {
				this.header = counter;
			}
		}
	};
</script>

<style lang="scss" src="./InsDropdownList.scss"></style>