<template>
    <div class="grid-x">
        <div class="large-12 medium-12 small-12 cell"
            v-if="(type === 'text' || type === 'string')">
            <InsInputWrap 
                field="text"
                v-bind:placeholder="label"
                v-bind:label="`${is_required === 'true' ? label +'*' : label }`"
                v-bind:required="is_required" 
                v-model="data.properties[name]"
                v-bind:errorMessage="`${label} is required.`"
                v-on:keyup.native="validateInput"  />
        </div>

        <div class="large-12 medium-12 small-12 cell"
            v-else-if="(type === 'number')">
            <InsInputWrap 
                field="number"
                v-bind:label="`${is_required === 'true' ? label +'*' : label }`"
                v-bind:required="is_required" 
                v-model="data.properties[name]"
                v-bind:errorMessage="`${label} is required.`"
                v-on:keyup.native="validateInput"  />
        </div>

        <div class="large-12 medium-12 small-12 cell"
            v-else-if="(type === 'decimal')">
            <InsInputWrap 
                field="number"
                v-bind:label="`${is_required === 'true' ? label +'*' : label }`"
                v-bind:required="is_required" 
                v-model="data.properties[name]"
                v-bind:errorMessage="`${label} is required.`"
                v-on:keyup.native="validateInput" />
        </div>

        <div class="large-12 medium-12 small-12 cell"
            v-else-if="(type === 'list_select')">
            <InsSelectWrap
                class="large-12 medium-12 small-12 cell"
                placeholder="- Select -"
                v-bind:label="`${is_required === 'true' ? label +'*' : label }`"
                v-bind:required="is_required === 'true' ? true : false"
                v-bind:errorMessage="`${label} is required.`"
                v-model="data.properties[name]"
                searchable>
                    <ins-select-option
                        v-for="(value) in values"
                        v-bind:key="value"
                        v-bind:label="value"
                        v-bind:value="value">
                    </ins-select-option>
                </InsSelectWrap>
        </div>

        <div class="large-12 medium-12 small-12 cell" 
            v-else-if="(type === 'list_checkbox')">
            <div 
                v-bind:class="`ins-checkbox-group-wrapper${is_required === 'true' ? ' required' : ''}`"
                v-bind:group-name="name">
                <label>{{ is_required === 'true' ? label +'*' : label }}</label>
                <InsCheckboxWrap                      
                    v-for="(value, index) in values"
                    v-bind:key="index"
                    v-bind:name="name" 
                    v-bind:label="value" 
                    v-bind:value="value"
                    v-bind:required="is_required === 'true' ? true : false"  
                    v-bind:checked="checkListValue(value)"
                    v-on:input="listCheckboxHandler" />     
                <div class="ins-form-error">{{ label }} is required.</div>
            </div>
        </div>

        <div class="large-12 medium-12 small-12 cell"
            v-else-if="(type === 'list_radio')">
            <div 
                v-bind:class="`ins-radio-group-wrapper${is_required === 'true' ? ' required' : ''}`"
                v-bind:group-name="name">
                <label>{{ is_required === 'true' ? label +'*' : label }}</label>
                <InsRadioWrap                             
                    v-for="(value, index) in values"
                    v-model="data.properties[name]"  
                    v-bind:key="index"
                    v-bind:name="name" 
                    v-bind:label="value" 
                    v-bind:required="is_required === 'true' ? true : false"
                    v-bind:checked="value === data.properties[name] ? true : false"
                    v-on:event="listRadioHandler" />  
                <div class="ins-form-error">{{ label }} is required.</div>
            </div>                                
        </div>
        
        <div class="large-12 medium-12 small-12 cell"
            v-else-if="(type === 'toggle')">
            <InsToggleWrap 
                class="text-left"
                v-bind:label="label"
                v-bind:name="name"
                v-model="data.properties[name]"  />
        </div>

        <div class="ins-datpicker-wrapper large-12 medium-12 small-12 cell"
            v-else-if="(type === 'date')">
            <InsDatePicker        
                field="date"         
                dateFormat="MM/DD/YYYY"
                v-bind:label="`${is_required === 'true' ? label +'*' : label }`" 
                v-bind:name="name"
                v-bind:required="is_required === 'true' ? true : false"
                v-model="data.properties[name]"
                v-bind:errorMessage="`${label} is required.`" />
        </div>
        <div class="large-12 medium-12 small-12 cell"
            v-else-if="(type === 'link')">
            <div class="link-wrap">                        
                <InsInputWrap 
                    field="link"
                    icon="icon-link"
                    v-bind:placeholder="label"
                    v-bind:label="`${is_required === 'true' ? label +'*' : label }`"  
                    v-model="data.properties[name]"                           
                    v-bind:errorMessage="`${label} is required.`"
                    v-on:keyup.native="validateInput" />
            </div>
        </div>
        <div class="large-12 medium-12 small-12 cell"
            v-else-if="(type === 'multi_line')">
            <InsTextareaWrap 
                field="text"
                v-bind:placeholder="label"
                v-bind:label="`${is_required === 'true' ? label +'*' : label }`"                
                v-bind:required="is_required === 'true' ? 'true' : 'false'"
                v-model="data.properties[name]"                        
                v-bind:errorMessage="`${label} is required.`"
                v-on:keyup.native="validateInput"  />
        </div> 
        <!-- end list of fields -->

        <!-- fallback component -->
        <div class="large-12 medium-12 small-12 cell"
            v-else>
            <InsInputWrap 
                field="text"
                v-bind:placeholder="label"
                v-bind:label="`${is_required === 'true' ? label +'*' : label }`"
                v-bind:required="is_required" 
                v-model="data.properties[name]"
                v-bind:errorMessage="`${label} is required.`" 
                v-on:input="validateInput" />
        </div>   
    </div>
</template>

<script>
import InsInputWrap from "../../InsInputWrap/InsInputWrap.vue"
import InsSelectWrap from "../../InsSelectWrap/InsSelectWrap.vue";
import InsTextareaWrap from "../../InsTextareaWrap/InsTextareaWrap.vue";
import InsToggleWrap from "../../InsToggleWrap/InsToggleWrap.vue";
import InsRadioWrap from "../../InsRadioWrap/InsRadioWrap.vue";
import InsCheckboxWrap from "../../InsCheckboxWrap/InsCheckboxWrap.vue";
import InsDatePicker from "../../InsDatePicker/InsDatePicker.vue";

import InsitesMixins from "../../../mixins/InsitesMixins.js";

export default {
    name: "DynamicFields",
    components: {
        InsInputWrap,
        InsSelectWrap,
        InsTextareaWrap,
        InsToggleWrap,
        InsRadioWrap,
        InsCheckboxWrap,
        InsDatePicker
    },
    mixins: [InsitesMixins],
    props: {
        value: [String, Boolean, Number, Array],
        values: Array,
        data: Object,
        name: String,
        label: String,
        type: [Boolean, String],
        is_required: [String],
        weight: [Number, Boolean]
    },
    methods: {
        listCheckboxHandler(event) {
            this.$emit('checkboxInputChange', event);
        },
        listRadioHandler(event) {
            this.$emit('radioInputChange', event);
        },  
        validateInput(field) {
            if (this.is_required === "true") {
                field = field.target ? field.target: field; 
                if(field.value.trim() === "") {
                    InsitesUtil.invalidFieldNotif(field);
                } else {
                    InsitesUtil.validFieldNotif(field);
                }
            }
        },
        checkListValue(value) {
            if (value) {
                let fieldValue = this.data.properties[this.name];
                if(this.data.properties[this.name] && !!this.data.properties[this.name]) {
                    fieldValue = fieldValue.split(',');
                    if (fieldValue.includes(value)) {
                        return true;
                    }
                }   
                return false;             
            }
            return false;
        }
    }
}
</script>

