<template>
    <div class="grid-container-full pages">
        <div class="grid-x">
            <div class="large-12 cell database-list-container">
                <!-- database filters -->
                <div class="database-filter">
                </div>
                <!-- end database filters -->
                <!-- no data placeholder -->
                <div v-show="!hasData">
                    <ins-card steady>
                        <h2 class="title-page h2desc">Welcome to Database</h2>
                        <p class="pdesc">Database are your customisations data which you can assign data columns and attributes. 
                            Each database can be used as a dynamic data storage which you can use across your website content.</p>
                    </ins-card>

                    <div class="pages-bottom">
                        <h2 class="pages-title-secondary h2desc">You don't have any database yet.</h2>
                        <p class="pdesc">Would you like to create your first database?</p>

                        <ins-button label="ADD NEW Database" solid
                        v-on:onClickInsButton="gotToCreateDatabase">
                        </ins-button>
                    </div>
                </div>
                <!-- end no data placeholder -->

                <!-- database list table -->
                <div class="table-list-container" v-show="hasData">
                    <div class="header-buttons">
                        <ins-button label="ADD NEW Database" solid
                        color="primary" size="normal"
                        v-on:onClickInsButton="gotToCreateDatabase">
                        </ins-button>
                    </div>

                    <div class="ins-table-container">
                        <ins-base-table ref="insBaseTable"
							searchbar-placeholder="Search by Database Name"
							v-on:onPaginationChange="tablePaginationHandler"
							v-on:onTableRowAction="tableRowActionHandler"
							v-on:onTableSort="tableSortHandler"
							v-on:onTableSearch="tableSearchHandler">
                        </ins-base-table>
                        <div class="spacer large"></div>
                    </div>
                </div>
                <!-- end database list table -->
            </div>
        </div>
    </div>
</template>

<script>
	import { fetchDatabaseListJSON, createDatabase, updateDatabase, searchDatabaseListJSON } from '../../services/DatabaseServices.js';

	import DatabaseManagerMixins from '../../mixins/DatabaseManagerMixins.js';
	import DatabaseItemsMixins from '../../mixins/DatabaseItemsMixins.js';
	import DatabaseDeleteMixins from "../../mixins/DatabaseDeleteMixins.js";

	export default {
		name: "ListDatabase",
		mixins: [DatabaseManagerMixins, DatabaseItemsMixins, DatabaseDeleteMixins],
		data() {
			return {
				hasData: false,
				databaseList: [],
				pageSize: 10,
				pageNumber: 1,
				sortedKeyword: "",
				sortedBy: "asc",
				searchKeyword: "",
				continueDeletion: false,
				is_enabled: "true"
			}
		},
		methods: {
			gotToCreateDatabase() {
				InsitesUtil.renderPage(`Add New Database`, '#/manage-database');
				this.$router.push("/manage-database");
			},
			gotToManageDatabase(data, action) {
				InsitesUtil.renderPage(`${action} Database`, `#/edit-database/${data.id}`);
				this.$store.commit("updateDatabaseFormData", { data, action });
				this.$router.push(`/edit-database/${data.id}`);
			},
			viewDatabaseItems(data) {
				InsitesUtil.renderPage(`${data.Name}`, `#/list-database-items/${data.id}`);
				// this.$store.commit("updateDatabaseFormData", { data, action });
				// this.$router.push(`/edit-database/${data.id}`);
			},
			constructTableData(item){                
				return {
					rawData: item,
					id: item.id,
					"Name": item.name
				}
			},
			setupInsBaseTable() {
				// this.$refs.insBaseTable.bulkActions = ["Delete"];
				this.$refs.insBaseTable.rowActions = ["View", "Edit", "Delete"]; 
				this.$refs.insBaseTable.tableHeaders = [
					this.constructTableHeader("Name", "string", true)
				];
			},
			constructTableHeader(label, type = "string", sortable, hasTag, hasLink, hasColumnAction, hasImage){
				return { label, type, sortable, hasTag, hasLink, hasColumnAction, hasImage }
			},
			tablePaginationHandler(event) {
				this.$refs.insBaseTable.loadingScreen = true;
				this.pageSize = event.detail.pageSize;
				this.pageNumber = event.detail.pageNumber;
				this.getDatabaseList();
			},
			tableSearchHandler(event) {
				this.searchKeyword = event.detail.value;
				if (event.detail.keyCode === 13 && event.detail.value !==  "") {
					this.pageNumber = 1;
                    this.$refs.insBaseTable.pageNumber = 1;
					this.getDatabaseList();
				} else if (!this.searchKeyword) {
					this.getDatabaseList();
				}
            },
			tableSortHandler(event) {
				this.sortedKeyword = event.detail.keyword;
				this.sortedBy = event.detail.order;
				this.getDatabaseList();
			},
			// tableBulkActionHandler(event) {
			// 	switch (event.detail.action) {
			// 		case "Delete":
			// 			this.archiveSelection(event.detail.selections);
			// 		break;
			// 	}
			// },
			tableRowActionHandler(event) {
				let data = event.detail.data;
				let action = event.detail.action;

				switch (event.detail.action) {
					case 'Delete':
						this.deleteItem(event.detail.data);
						break;

					case 'Edit':
						this.gotToManageDatabase(data, action);
						break;

					case 'View':
						// view database items list
						this.viewDatabaseItems(event.detail.data);
						break;             
				}
			},
			async deleteItem(data) {
				let self = this;

				swal({
					title: "",
					html: `
					<ins-loader
						state-icon="warning"
						icon-color="warning"
						state-message="Are you sure you want to delete this database? This will also delete existing data.">
					</ins-loader>`,
					type: "",
					width: 330,
					showCancelButton: true,
					cancelButtonText: "Cancel",
					confirmButtonClass: "negative",
					confirmButtonText: "Delete"
				}).then(result => {
					if (result.value) {
						self.processDeleteDatabase(data);
					}
				});
			},
			async processDeleteDatabase(data) {
				let response = await this.deleteHandler(data.id, this.convertToCustomizationName(data.rawData.physical_file_path));

				if (response.state) {
					toastIt('success', `Database has been deleted.`, InsitesUtil.ops());
					this.getDatabaseList();
				} else {
					toastIt("error", "Failed to delete database. Please try again.", InsitesUtil.ops());
				}
			},
			// async deleteItem(data, message) {
			// 	this.is_enabled = false;
			// 	let response;
			// 	let deleteDatabase = await this.updateDatabaseHandler()
			// 		.then(response => {
			// 			this.deleteSuccessHandler(response, message);
			// 		})
			// 		.catch(error => {
			// 			this.deleteErrorHandler(error);
			// 		});
			// },
			// deleteSuccessHandler(response, message) {
			// 	toastIt('success', `${message} been deleted.`, InsitesUtil.ops());
			// 	this.getDatabaseList();
			// },
			// deleteErrorHandler(error) {
			// 	console.error("Delete Database Error: ", error);
			// 	toastIt("error", "Failed to delete database. Please try again.", InsitesUtil.ops());
			// },
			// bulkDeleteHandler(selections) {
			// 	let counter = 0;
			// 	selections.forEach(data => {
			// 		if (this.continueDeletion){
			// 			this.deleteItem(data.rawData, "The selected database have");
			// 			counter++;
			// 			if (selections.length === counter) {
			// 				this.continueDeletion = false;
			// 				this.is_enabled = true;
			// 			}
			// 		}
			// 	});
			// },
			// archiveItem(data) {
			// 	swal({
			// 		title: "",
			// 		html: `
			// 		<ins-loader
			// 			state-icon="warning"
			// 			icon-color="warning"
			// 			state-message="Are you sure you want to delete this database? \n This will also delete existing data.">
			// 		</ins-loader>`,
			// 		type: "",
			// 		width: 330,
			// 		showCancelButton: true,
			// 		confirmButtonClass: "negative",
			// 		confirmButtonText: "Delete"
			// 	}).then(result => {
			// 		if (result.value){
			// 			this.deleteItem(data.rawData, "Database has");
			// 		}
			// 	});
			// },
			// archiveSelection(selections) {
			// 	swal({
			// 		title: "",
			// 		html: `
			// 		<ins-loader
			// 			state-icon="warning"
			// 			icon-color="warning"
			// 			state-message="Are you sure you want to delete these database? This will also delete existing data.">
			// 		</ins-loader>`,
			// 		type: "",
			// 		width: 330,
			// 		showCancelButton: true,
			// 		confirmButtonClass: "negative",
			// 		confirmButtonText: "Delete"
			// 	}).then(result => {
			// 		if (result.value) {
			// 		this.continueDeletion = true;
			// 		this.bulkDeleteHandler(selections);
			// 		}
			// 	});
			// },
			updateTableData(data) {
				let databaseList = data;
				this.$refs.insBaseTable.totalCount = databaseList.total_entries;
				this.$refs.insBaseTable.tableData = databaseList.results.map(this.constructTableData);                
				this.$refs.insBaseTable.loadingScreen = false;
				this.hasData = true;
			},
			async fetchDatabaseList() {
				let response = await fetchDatabaseListJSON(this.pageNumber, this.pageSize, this.sortedKeyword, this.sortedBy, this.searchKeyword);

				return new Promise(resolve => {
					if (response.state) {
						resolve(response.results);
					} else {
						toastIt("error", "Failed to fetch data. Please try again.", InsitesUtil.ops());
						resolve();
					}
				});
			},
			async getDatabaseList() {
				this.hasData = true;
				this.setInsBaseTableLoader(
					"Processing Your Data",
					"Just a moment, we're fetching and processing your data.",
					"processing"
				);
				let data = await this.fetchDatabaseList();
				if(data.items.results.length) {
					this.updateTableData(data.items);
				} else {
					this.databaselist = [];
					this.hasData = false;
				} 
			},
			async searchDatabaseList() {
				let searchData = await this.fetchDatabaseList();
				if(searchData.items.results.length) {
					this.updateTableData(searchData.items);
				} else {
					if (this.searchKeyword) {
						let message = `No results for "${this.searchKeyword}"`;
						this.setInsBaseTableLoader('', message, 'no-data');
					} else {
						this.hasData = false;
					} 
				}
			},
			async updateDatabaseHandler() {
				let id = ""; // insert get id here
				let formData = this.buildFormData();
				formData['payload']['id'] = id;
				let updateDatabase = await updateDatabase(formData);

				// insert code form submit result handling
			},
			buildFormData() {
				let physical_file_path = this.setPhysicalPath('custom_model_types', this.databaseName)
				let dbName = '';
				return JSON.stringify(this.buildDatabaseSchema(dbName, physical_file_path, this.is_enabled));            
			},
			setInsBaseTableLoader(title, message, icon){
				this.$refs.insBaseTable.loadingScreen = true;
				this.$refs.insBaseTable.loaderTitle = title;
				this.$refs.insBaseTable.loaderMessage = message
				this.$refs.insBaseTable.loaderIcon = icon;
			}
		},
		mounted() {
			this.setupInsBaseTable();
			this.getDatabaseList();
		}
	}
</script>

