import { fetchDatabaseItemsListJSON, truncateDatabase, deleteDatabase } from "../services/DatabaseServices";

export default {
    name: "DatabaseDeleteMixins",
    methods: {
        async deleteHandler(id, physical_file_path) {
            let response = await fetchDatabaseItemsListJSON(physical_file_path);

            return new Promise(async resolve => {
                if (response.state) {
                    let _response = { state: false };
                    if (response.results.items.results.length) {
                        _response = await this.truncateDatabaseHandler(id, physical_file_path);
                    } else {
                        _response = await this.deleteDatabaseHandler(id);
                    }

                    resolve(_response);
                } else {
                    resolve({ state: false });
                }
            });
        },
        async truncateDatabaseHandler(id, physical_file_path) {
            let response = await truncateDatabase({ name: physical_file_path });

            return new Promise(resolve => {
                if (response.state) {
                    this.deleteDatabaseHandler(id);
                } else {
                    resolve({ state: false });
                }
            });
        },
        async deleteDatabaseHandler(id) {
            let response = await deleteDatabase({ id: id });
            
            return new Promise(resolve => {
                if (response.state) {
                    resolve({ state: true });
                } else {
                    resolve({ state: false });
                }
            });
        }
    }
}